export default class Login extends Component {

  componentDidMount(){

    ui.start('#firebaseui-auth-container', {
      signInOptions: [
        firebase.auth.EmailAuthProvider.PROVIDER_ID
      ],
      signInSuccessUrl: 'http://localhost:8080/',

      callbacks: {
        // signInFailure callback must be provided to handle merge conflicts which
        // occur when an existing credential is linked to an anonymous user.
        signInFailure: function(error) {
          // For merge conflicts, the error.code will be
          // 'firebaseui/anonymous-upgrade-merge-conflict'.
          if (error.code != 'firebaseui/anonymous-upgrade-merge-conflict') {
            return Promise.resolve();
          }
          // The credential the user tried to sign in with.
          var cred = error.credential;
          // Copy data from anonymous user to permanent user and delete anonymous
          // user.
          // ...
          // Finish sign-in after data is copied.
          return firebase.auth().signInWithCredential(cred);
        }
      },

      // Other config options...
    });


    //Google singin provider
    var ggProvider = new firebase.auth.GoogleAuthProvider();
    //Facebook singin provider
    var fbProvider = new firebase.auth.FacebookAuthProvider();
    //Login in variables
    const btnGoogle = document.getElementById('btnGoogle');
    const btnFaceBook = document.getElementById('btnFacebook');

    // //Sing in with Google
    btnGoogle.addEventListener('click', e => {
        firebase.auth().signInWithPopup(ggProvider).then(function(result) {
            var token = result.credential.accessToken;
            var user = result.user;
            console.log('User>>Goole>>>>', user);
            userId = user.uid;

        }).catch(function(error) {
            console.error('Error: hande error here>>>', error.code)
        })
    }, false)
  }


  handleClick(e){
    e.preventDefault
    if(e.currentTarget.classList.contains('active')){
      e.currentTarget.classList.remove('active')
    } else {
      e.currentTarget.classList.add('active')
    }
  }

  render(props) {
    console.log('login page.')
    return (
      <div className="word-wrap">
        <div id="firebaseui-auth-container"></div>
       <div className="form-group text-center" style="display:none;">
            <button className="btn btn-block btn-social btn-google" id="btnGoogle" type="button"> <span className="fa fa-google"></span>Login with Google</button>
        </div>
        <div className="form-group text-center" style="display:none;">
            <button className="btn btn-block btn-social btn-facebook" id="btnFacebook" type="button"> <span className="fa fa-facebook"></span>Login with Facebook</button>
        </div>
      </div>
    );
  }
}



